import React, {useState} from 'react'
import { Link } from 'gatsby'
import {FaBars} from 'react-icons/fa'
import { menuData } from '../data/MenuData'
import { Button } from './Button'
import './styles/Header.scss'



export default function Header(props) {
  const [active, setActive] = useState(false);

  const isActive = () => {
      if (active) {
        return 'active';
      } else {
        return '';
      }
  }
  
  return (
    <header id="Header" css={`background-color: ${props.color};`}>
        <div className={`navbar ${isActive()}`}>
          <Link to='/'>EXPLORIX</Link>
          <FaBars className="bars" onClick={() => {setActive(!active)}}/>
          <div className="navMenu">
            {menuData.map((item, index) => (
                <Link to={item.link} key={index} className="nav-link">
                  {item.title}
                </Link>
            ))}
          </div>
            <Button primary='true' round='true' to="/trips" big='false' css={`height: 48px;`} className="header-btn">Book a Flight</Button>

            <div className="allnav-menu">
              <div className="small-nav-menu">
                {menuData.map((item, index) => (
                  <Link to={item.link} key={index} className="nav-link">
                    {item.title}
                  </Link>
                ))}
              </div>
              <Button primary='true' round='true' to="/trips" big='false' css={`height: 48px; width: 175px; text-align: center;`}>Book a Flight</Button>
          </div>
        </div>
    </header>
  )
}
